<template>
  <Layout title="修改手机" :has-nav="false">
    <div class="reset-phone">
      <div class="password-form">
        <input v-model="newPhone"
               class="first-password"
               type="tel"
               maxlength="11"
               placeholder="请输入新手机号码">
        <input v-model="verificationCode"
               class="verificationCode"
               type="tel"
               maxlength="6"
               placeholder="请输入验证码">
        <button class="codeBtn"
                :disabled="checkPhoneNumber"
                @click="getVerificationCode">
          {{ verificationCodeText }}
        </button>
      </div>
      <button :disabled="check"
              class="submit"
              @click="submit">提交
      </button>
    </div>
  </Layout>
</template>

<script>
export default {
  name: 'PasswordView',
  data() {
    return {
      verificationCode: '',
      newPhone: '',
      verificationCodeText: '获取验证码',

    }
  },
  computed: {
    checkPhoneNumber: function () {
      if (this.verificationCodeText !== '获取验证码') {
        return true
      }
      if (this.newPhone.length !== 11) {
        return true
      } else {
        return !(/^1[3456789]\d{9}$/.test(this.newPhone))
      }
    },
    check() {
      if (this.verificationCode.length !== 6) {
        return true
      }
      if (this.newPhone.length !== 11) {
        return true
      }
    },
  },
  methods: {
    getVerificationCode() {
      console.log('获取验证码')
      this.verificationCodeText = '60s'
      let count = 60
      let timer = setInterval(() => {
        count--
        if (count === 0) {
          clearInterval(timer)
          this.verificationCodeText = '获取验证码'
        } else {
          this.verificationCodeText = `${count}s`
        }
      }, 1000)
    },
    submit() {
      console.log('提交')
    },

  }
}
</script>

